  padding: 0;
  box-sizing: border-box; }

body {
  font-family: "Cabin", sans-serif;
  letter-spacing: 0.05em;
  font-weight: 600;
  background: #f2f5f8; }

.container {
  position: relative;
  display: flex;
  -webkit-justify-content: space-around;
  justify-content: space-around;
  flex-flow: row wrap;
  max-width: 1000px;
  margin: 0 auto;
  padding: 70px 10px; }

.title {
  top: 10px;
  left: 50%;
  position: absolute;
  font-size: 30px;
  line-height: 40px;
  color: #383737;
  transform: translateX(-50%);
  text-transform: uppercase;
  text-shadow: 2px 2px 2px #b4bbc2; }

.template-link {
  position: relative;
  width: 300px;
  height: 300px;
  margin-bottom: 30px;
  box-shadow: 0 0 5px 2px #383737; }
  .template-link__name {
    bottom: 0;
    left: 0;
    position: absolute;
    font-size: 30px;
    line-height: 70px;
    color: #fff;
    right: 0;
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 0 0 6px 6px;
    text-align: center;
    display: none; }
  .template-link:hover {
    box-shadow: 0 0 7px 3px #383737; }
  .template-link:hover > .template-link__name {
    display: block; }
  .template-link--base {
    background: url("../img/base.jpg");
    -webkit-background-size: cover;
    -o-background-size: cover;
    background-size: cover; }
  .template-link--headers {
    background: url("../img/headers.jpg");
